.fifthDiv{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 2em;
    gap: 2em;
    margin-top: 2em;
}


.txt-input1{
    width:100% !important;
    margin-top: 20px;
    border-radius: 20px;
    padding-left: 10px;
    height:200px;
    border: 2px solid #0471ffab;

}

.txt-input{
    border: 2px solid #0471ffab;
    width:100% !important;
    margin-top: 20px;
    border-radius: 20px;
    padding-left: 10px;
    height:50px;
}

.sendBtn{
    margin-top: 2em;
    width: 25%;
}

.textDiv{
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.map{
    width: 100%;
    height: 100%;
}

@media only screen and (max-width: 800px){
    .fifthDiv{
        display: flex;
        flex-direction: column;
    }

    .sendBtn{
        width: 100%;
    }
}