.websitesList{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, .3fr));
    row-gap: 2em;
    column-gap: 2em;
    margin-top: 100px;
}

.card{
    transform: scale(1);
    transition: all .5s;
}

.card:hover{
    transform: scale(1.2);
}


@media only screen and (max-width: 500px){
    .websitesList{
        grid-template-columns: 1fr;
    }
}