.LargeCard{
    border-radius: 10px;
    box-shadow: 0 4px 10px 0 rgb(0 0 0 / 20%), 0 4px 20px 0 rgb(0 0 0 / 19%);
    transition: box-shadow .3s;
    background-color: white;
    font-size: smaller;
}

.LargeCard:hover{
    box-shadow: 0 4px 10px 0 rgba(65, 105, 225, 0.664), 0 4px 20px 0 rgba(65, 105, 225, 0.637);
}