.contentInfourthDiv{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3em;
    padding-bottom: 2em;
    border-radius: 1em;
}

.sixthDiv{
    margin-top: 6em;
    padding-top: 2em;
}

@media screen and (max-width: 800px){
    .contentInfourthDiv{
        grid-template-columns: 1fr;
    }
}