.FirstDivHolder{
    border: 1px solid;
    width: 100%;
    background-color: #111;
    display: flex;
    color: white;
}

.imageHolder{
    width: 50%;
    height: 100%;
}

.imageHolder > img{
    height: 100%;
}

.textHandler{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    padding: 5em;
}

.image{
    position: absolute;
    z-index: 0;
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 800px){
    .FirstDivHolder{
        flex-direction: column;
    }
    .textHandler{
        width: 100%;
        padding: 1em;
    }
    .imageHolder{
        display: none
    }
}